// 引入 API
import Web3 from "web3"
import { abi } from "../../build/contracts/DAO.json";

// init web3
var web3 = new Web3('http://localhost:8545');

// init Contract
var DAOContract = new web3.eth.Contract(abi,"0x8acf5f84036bce8adb09b4d33aaa159616422dac");

web3.eth.getAccounts(function(err, res){
  if(err) {console.log(err)}
  web3.eth.defaultAccount = res[0]
}).then(function(){
  web3.eth.getBalance(web3.eth.defaultAccount,function(err, res){
    console.log(res)
  })
}).then(function(){
  web3.eth.getBalance("0x8acf5f84036bce8adb09b4d33aaa159616422dac",function(err, res){
    console.log(res)
  })
}).then(
  transfer()
)

// transfer
function transfer() {
  DAOContract.methods.transfer("0x8acf5f84036bce8adb09b4d33aaa159616422dac",1).call().then(function(res){
    console.log(res);
  })
}

export default () => (
  <div className="height:100%;width:100%;">
    <h1 style={{"textAlign":"center","marginTop":"10rem"}}>DAO</h1>
    <button value="hello">aa</button>
  </div>
)



